.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 95vh; }

.main-container-child {
  display: flex;
  flex-flow: column;
  min-width: 250px;
  max-width: 500px;
  width: 100%;
  height: 100vh;
  max-height: 600px;
  min-height: 500px;
  align-self: center;
  align-items: center;
  justify-content: space-around;
  background-color: #eee; }

.user-validation-container {
  display: flex;
  flex-flow: column;
  min-width: 250px;
  max-width: 500px;
  width: 100%;
  height: 100vh;
  max-height: 600px;
  min-height: 500px;
  align-self: center;
  align-items: center;
  justify-content: space-between;
  background-color: #eee;
  padding: 25px; }

.layout-children-container {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  height: 100%;
  align-items: center;
  margin-top: 20px;
  flex-wrap: wrap; }

.invitation-code-input {
  width: 80%;
  border: none;
  outline: none;
  border-bottom: 1px solid darkcyan;
  background-color: #eee; }

.btn {
  min-width: 30px;
  border: none;
  outline: none;
  background-color: darkcyan;
  color: white;
  padding: 10px 20px 10px 20px;
  transition: all linear 0.3s; }

.btn:hover {
  background-color: white;
  color: darkcyan;
  border: 1px solid darkcyan;
  transition: all linear 0.3s;
  cursor: pointer; }
