.sidebar-header {
  display: flex;
  height: 150px;
  justify-content: center;
  align-items: center; }

.sidebar-card {
  width: 256px;
  height: 363px;
  display: flex;
  flex-flow: column wrap;
  background: #f0f9f9;
  border-radius: 9px;
  padding: 30px !important; }
  .sidebar-card img {
    width: 140px;
    height: 140px;
    border-radius: 70px;
    background-color: #ef5359; }
  .sidebar-card .card-name {
    font-family: "Open Sans";
    margin-top: 20px;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    color: #344563;
    margin-bottom: 0 !important; }
  .sidebar-card .card-title {
    background: #6c7b8a;
    border-radius: 50px;
    height: 19px;
    width: 131px;
    height: 20px;
    margin-bottom: 34px;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    color: #FFFFFF;
    padding-left: 7px; }

.memberSince {
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #7D8DA1; }

.f-date {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  color: #344563; }

.sidebar-card-footer {
  width: 196px;
  height: 60px;
  background: #fffbf9;
  opacity: 0.5;
  border: 1px solid #b3e0e0;
  box-sizing: border-box;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .sidebar-card-footer p {
    padding: 0px;
    margin: 0px; }
  .sidebar-card-footer .f-date {
    font-family: Open Sans;
    font-style: bold;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #2f394b; }

.sidebar-wrapper {
  height: '100%';
  overflow-y: scroll; }

.sidebar-content {
  height: 'calc(100vh - 200px) !important';
  overflow: hidden !important; }

.scrollBarStyle::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3);
  background-color: #F5F5F5; }

.scrollBarStyle::-webkit-scrollbar {
  width: 3px;
  background-color: #F5F5F5;
  overflow-y: scroll; }

.scrollBarStyle::-webkit-scrollbar-thumb {
  background-color: #009999;
  overflow-y: scroll; }

.cursorPoint:hover {
  cursor: pointer; }

.navUlListHeading {
  height: 13px;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 1px;
  color: #6C7B8A;
  mix-blend-mode: normal;
  opacity: 0.75; }

.sidebar-card-content {
  width: 196px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 303px; }
